





































import photoCard from "../components/photo_card.vue"
import images from "../images.json"
import definition from "../components/definition.vue"
import Vue from 'vue'
import 'viewerjs/dist/viewer.css'
import { directive as viewer } from "v-viewer"

type image={
  title: String
  id: String
  description: String
  relevance:Number  
}
type film = {
  id: String,
  description: String,
  size: Number,
  year: String,
  place: String,
  tags: Array<String>,
  fotoCover: Number
}
export default Vue.extend({
  components: {
    photoCard,
    definition,
  },
  directives: {
      viewer: viewer({
        debug: true,
      }),
    },
  props: {
    filmInfo: {
      type: Object as () => film,
    }
  },
  data() {
    return {
      img: images.images,
    }
  },
  methods: {
    //this will be done in the future by the backend
    filterImg: function (imagesArray, filmid):Array<image>{
      let images = imagesArray.filter(function (image) {
        return image.id.includes(`${filmid}_`)
      })
      return images;
    },
    
  },
})
