

















type film = {
  id: String,
  description: String,
  size: Number,
  year: String,
  place: String,
  tags: Array<String>,
  fotoCover: Number
}
export default {
  props: {
    filmInfo: {
      type: Object as () => film,
    }
  },
  data() {
    return {
      mainProps: {
          center: true,
          fluidGrow: true,
          blank: true,
          blankColor: '#bbb',
          width: '100%',
          height: '100%',
        }
    }
  },

  methods: {
    coverSrc(info:film) {
      return require(`../assets/${info.id}/${info.fotoCover}.jpg`)
    },
  },
}
