






import Vue from "vue"

export default Vue.extend({
  name: "photo-card",
  data() {
    return {
      mainProps: {
        center: true,
        fluidGrow: true,
        blank: false,
        blankColor: "#bbb",
        width: 600,
        height: 400,
      },
    }
  },
  props: {
    pic: Object,
  },
  methods: {
    getImg:function(pic) {
      let path = pic.id.split("_");
      return require(`../assets/${path[0]}/${path[1]}.jpg`);
    },
  },
})
